/* src/components/HealthMetrics.css */
.health-metrics {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
}

.health-metrics input {
    margin: 10px 0;
    padding: 8px;
    width: 100%;
}

.health-metrics button {
    padding: 10px 15px;
    background-color: #2980b9;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
